.form {
  background-color: hwb(260 50% 48% / 0.751);
  padding: 10px;
  border-radius: 5px;
  margin: 20px 0;
  margin-bottom: 0px;
  text-align: center;
  box-shadow: 2px 2px 20px black;
  height: 400px;
}

@media (max-width: 767px) {
  .form {
    height: 520px;
  }
}

.form__header {
  background-color: hsla(65, 26%, 91%, 0);
  color: whitesmoke;
  font-size: 24px;
  max-width: 250px;
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  font-weight: 700;
  text-align: center;
  display: inline-block;
  box-shadow: 1px 1px 10px rgb(32, 31, 31);
}

.form__label {
  width: 100%;
  max-width: 200px;
  display: inline-block;
  margin-right: 5px;
  text-align: left;
}

.form__field {
  border: 1px solid #ccc;
  padding: 10px;
  width: 100%;
  max-width: 250px;
  border-radius: 5px;
}